// https://github.com/zhetengbiji/mpvue-picker
import helper from '../../common/helper.js';
export default {
	components: {},
	data() {
		return {
			UserId: '',
			userfoArray: '',
			nickname: '',
			array: ['女', '男', ''],
			index: 0,
			mobile: '',
			userdata: "",
			loginMark: "",
			token: "",
			img: ''
		};
	},
	onLoad() {
		this.userinfo();
		this.UserId = helper.getstate().userid;
		this.loginMark = uni.getStorageSync('loginMark')
		this.token = uni.getStorageSync('token')
	},
	methods: {
		// 打开擅长领域
		openlingyu() {
			uni.navigateTo({
				url: 'Domain',
			});
		},

		// 修改个人信息表单提交
		formSubmit: function(e) {
			this.userdata = e.detail.value;
			var _this = this
			uni.showModal({
				title: '提示',
				content: '是否确定修改。',
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: helper.websiteUrl + '/usercenter/UpdateUserCenter',
							method: 'GET',
							data: {
								"loginMark": helper.getloginMark(),
								"token": _this.token,
								"data": _this.userdata
							},
							success: res => {
								if (res.data.code == 200) {
									uni.showToast({
										icon: 'none',
										title: '修改成功'
									});
								} else {
									uni.showToast({
										icon: 'none',
										title: res.data.info
									});
								}
							},
							fail: () => {
								uni.showToast({
									icon: 'none',
									title: '网络异常,请稍后重试'
								});
							}

						});
					}
				}
			});

		},
		// 性别选择
		bindPickerChange: function(e) {
			this.index = e.target.value
		},
		// 获取个人信息
		userinfo() {
			var userId = helper.getstate().userid;
			uni.request({
				url: helper.websiteUrl + '/usercenter/getuserinfo',
				method: 'GET',
				data: helper.postdata({
					id: userId,
				}),
				success: res => {
					if (res.data.code == 200) {
						this.img = res.data.data.baseinfo.Headimgurl
						this.index = res.data.data.baseinfo.Sex
						this.userfoArray = res.data.data.baseinfo;
					}

				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},
		setData: function(obj) {
			let that = this;
			Object.keys(obj).forEach(function(key) {
				that.$set(that.$data, key, obj[key])
			});
		},
		// 获取用户上传头像地址
		getImage: function() {
			var _this = this
			uni.chooseImage({
				success: function(res) {
					uni.uploadFile({
						url: helper.websiteUrl + '/annexes/UploadImg', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						fileType: "uploadFile",
						name: 'upload',
						formData: helper.postdata({
							"folderId": "123465",
						}),
						success: function(res) {
							var arr = JSON.parse(res.data)
							var arrs = arr.info
							var url = 'https://cszx.yiruit.net'
							var imgurl = url + arrs
							_this.img = imgurl
						}
					})
					_this.setData({
						img: res.tempFilePaths[0],
					})
				},
			})
		},
		openphone() {
			uni.navigateTo({
				url: 'zhanghuanquan',
			});
		}
	}
}
